import React, { useState } from 'react';
import './App.css';

const DEFAULT_VALUE = '';

function Convertor() {
  const [convertedValue, setConvertedValue] = useState(DEFAULT_VALUE);
  const [error, setError] = useState(null);

  return (
    <div className="convertor">
      <input
        className="convertor--input"
        autoFocus
        placeholder="> Enter Global ID"
        spellcheck="false"
        onChange={(event) => {
          const { value: globalId } = event.target;
          let localID = DEFAULT_VALUE;
          if (globalId) {
            try {
              localID = atob(globalId)
            } catch (err) {
              setError('Invalid global ID entered');
            }
          } else {
            setError(null);
          }
          setConvertedValue(localID);
        }}
      />
      <div className="convertor--output">
        {!error && convertedValue}
        {error && <span className="convertor--error">{error}</span>}
      </div>
    </div>
  );
}

function App() {
  return (
    <div className="page">
      <h1 className="title">Convert Global ID to Local ID</h1>
      <Convertor />
      <footer className="footer">
        created by <a href="https://iamdi.dev">@iamdi</a>
      </footer>
    </div>
  );
}

export default App;
